import request from "@/utils/request";

// 
export function getNoteList() {
  return request({
    url: "/note",
    method: "get"
  });
}

// 
export function createNoteFolder(data) {
  return request({
    url: "/folder",
    method: "post",
    data: data,
    header: {
        Authorization: localStorage.getItem("token")
    }
  });
}

// 
export function deleteNoteFolder(id) {
  return request({
    url: "/folder/" + id,
    method: "delete",
    header: {
        Authorization: localStorage.getItem("token")
    }
  });
}